import Vue from 'vue'
import Router from 'vue-router'
import Shoe from "../components/shoe.vue"
import Phone from "../components/phone.vue"
import BuyRecord from "../components/buyRecord.vue"
Vue.use(Router)

export default new Router({
  mode: 'hash', // 去掉url中的#
  routes: [
    {
      path: '/shoe',
      name: 'shoe',
      component: Shoe
    },{
      path: '/phone',
      name: 'phone',
      component: Phone
    },{
      path: '/buyRecord',
      name: 'buyRecord',
      component: BuyRecord
    }
  ]
})